#container {
        width: 800px;
        height: 450px;
        background-color: white;
        display: flex;
        border-radius: 10px;
        margin-left: 250px;
        margin-top: 30px;
    
}


#box {
    display: flex;
    flex-direction: column;

}


#film {
    color: red;
    font-family: cursive;
    margin-left: 100px;

}


#rating {
    color: black;
    background-color: lime;
    background-size: auto;
    font-family: cursive;
    display: flex;
    justify-content: center;
    border-radius: 15px;
    margin-left: 100px;

}


#genre {
    color: black;
    background-color: blueviolet;
    background-size: auto;
    font-family: cursive;
    display: flex;
    justify-content: center;
    border-radius: 15px;
    margin-left: 100px;

}


#image {
    width: 250px;
    height: 350px;
    border-radius: 10px;
    margin-left: 15px;
    margin-top: 15px;
}


#delete {
    color: black;
    background-color: red;
    width: 150px;
    height: 30px;
    font-family: monospace;
    border: none;
    border-radius: 20px;
    margin-top: 20px;
    margin-left: 100px;
    
}